.animatedbg {
    width: 100vw;
    height: 100vh;
    color: #fff;
    background: -webkit-gradient(linear, left bottom, left top, from(#ff0000), color-stop(#004aff), color-stop(#ffed00), color-stop(#ff00d5), color-stop(#00e7ff), color-stop(#19ff00), color-stop(#0e8e00), color-stop(#ba6fee), to(#ff8900));
    background: -webkit-linear-gradient(bottom, #ff0000, #004aff, #ffed00, #ff00d5, #00e7ff, #19ff00, #0e8e00, #ba6fee, #ff8900);
    background: -o-linear-gradient(bottom, #ff0000, #004aff, #ffed00, #ff00d5, #00e7ff, #19ff00, #0e8e00, #ba6fee, #ff8900);
    background: linear-gradient(0deg, #ff0000, #004aff, #ffed00, #ff00d5, #00e7ff, #19ff00, #0e8e00, #ba6fee, #ff8900);
    background-size: 400% 400%;
    -webkit-animation: Gradient 35s ease infinite;
    animation: Gradient 35s ease infinite;
}

@-webkit-keyframes Gradient {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0 50%
    }
}

@keyframes Gradient {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0 50%
    }
}




